import React, { useState, useEffect } from "react";import "./component.JapaneseText.css";// Container for single paragraphfunction JpTxtParagraph(props) {  return <p className={props.paragraphType}>{props.text}</p>;}// Container for all paragraphsfunction JpTxtParagraphs(props) {  // Split lines of text and render as paragraphs  let paragraphs = [];  let ps = props.text.split("/n");  for (let i = 0; i < ps.length; i++) {    paragraphs.push(<JpTxtParagraph text={ps[i]} key={i} />);  }  return <div className="wrapper">{paragraphs}</div>;}function JapaneseText(props) {  // States -----------------------  const [state, setState] = useState({    text: "loading.."  });  // Hooks ------------------------  useEffect(() => {    fetch(process.env.PUBLIC_URL + "/" + props.url)      .then(response => response.json())      .then(data => setState(data));  }, [props.url]);  return (    <div className="JapaneseText">      <JpTxtParagraphs text={state.text} />    </div>  );}export default JapaneseText;